<template lang="html">
  <div class="page" ref="popWrapper">
    <div class="content">
<a name="process20"></a>

<p><br></p>
<p><span class="rvts17">野兽（Beast）:</span></p>
<p><img width="538" height="573" alt="" hspace="1" vspace="1" src="../../../assets/pic1/abcamber66.jpg"><span class="rvts18"></span></p>
<p><span class="rvts43"></span>
<br><span class="rvts21">女王曾是野兽的表妹，然而野兽再也看不惯她那残暴的统治。反抗女王的失败几乎把野兽彻底毁掉</span><span class="rvts24">&#8212;&#8212;</span><span class="rvts21">所以他开始了在遥远海洋上的新生活。现在，宿敌归来。如果不去阻止她，没有人会阻止她。</span>
<br><span class="rvts46"></span>
<br><span class="rvts29">种族：人类</span>
<br><span class="rvts21">默认职业：战法师（</span><span class="rvts20">BattleMage</span><span class="rvts21">）</span>
<br><span class="rvts48"></span>
<br><span class="rvts29">种族天赋：</span>
<br><span class="rvts21">野兽拥有矮人族的起始天赋。</span>
<br><span class="rvts48"></span>
<br><span class="rvts29">特有标签：</span>
<br><span class="rvts21">野兽只能被选为男性，并附带野兽特有标签。该标签可以使得他拥有一些特定的对话和行动。另外他还具备贵族和野蛮人的标签。</span>
<br><span class="rvts48"></span>
<br><span class="rvts29">特殊技能：</span></p>
<div><table width="1206" border="1" cellpadding="0" cellspacing="-1" style="border-width: 0px; border-collapse: collapse;">
<tr valign="top">
<td width="109" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><span class="rvts26">图标</span></p>
</td>
<td width="195" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><span class="rvts26">英文</span></p>
</td>
<td width="109" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><span class="rvts26">中文</span></p>
</td>
<td width="738" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><span class="rvts26">说明</span></p>
</td>
</tr>
<tr valign="top">
<td width="109" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><img width="64" height="64" alt="" hspace="1" vspace="1" src="../../../assets/pic1/abcamber67.jpg"></p>
</td>
<td width="195" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><span class="rvts20">Petrifying Touch</span></p>
</td>
<td width="109" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><span class="rvts42">石化之触</span></p>
</td>
<td width="738" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><span class="rvts21">使目标石化，对他造成土元素伤害，</span>
<br><span class="rvts21">并移除目标身上的流血，燃烧，中毒和残疾效果</span></p>
</td>
</tr>
<tr valign="top">
<td width="109" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><img width="64" height="64" alt="" hspace="1" vspace="1" src="../../../assets/pic1/abcamber68.jpg"></p>
</td>
<td width="195" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><span class="rvts20">Blinding Squall</span></p>
</td>
<td width="109" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><span class="rvts42">致盲风暴</span></p>
</td>
<td width="738" valign="middle" style="border-width : 1px; border-color: #000000; border-style: solid;">
<p><span class="rvts21">使目标区域敌人受到气元素伤害，并施加致盲效果，持续</span><span class="rvts20">1</span><span class="rvts21">轮</span></p>
</td>
</tr>
</table>
</div>
<p class="rvps2"><span class="rvts48"></span>
<br><span class="rvts29">使用技巧：</span>
<br><span class="rvts20">1. </span><span class="rvts21">野兽可以作为近战的肉盾战士，也可作为可以造成混乱和成吨伤害的刺客。</span>
<br><span class="rvts20">2. </span><span class="rvts21">利用野兽独有的致盲风暴技能，可以使区域范围内的敌人致盲，这一点可以很好的保护正在遭受攻击的后排队伍。</span></p>
<p><br></p>
<p><br></p>

    </div>
  </div>
</template>
<script>
import BScroll from 'better-scroll'
export default {
  data () {
    return { clickcount : 1  };
  },
  methods: {
    _initScroll() {
      this.popScroll = new BScroll(this.$refs.popWrapper, {
        click: true,
        probeType: 3
      });
    },
    timer(){
      if (this.clickcount < 3) {
        this.popScroll.refresh()
        this.clickcount = this.clickcount + 1
      }
    }
  },
  created() {
    this.$nextTick(() => {
      this._initScroll();
    });
    setInterval(this.timer, 2000);
  }
};
</script>
<style>
@import '../../../../static/css/common.css';
</style>



